"use client"

import { useState, useEffect, useCallback } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Badge } from "@/components/ui/badge"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
} from "@/components/ui/dialog"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { SearchableSelect } from "@/components/ui/searchable-select"
import { SidebarToggle } from "@/components/app-sidebar"
import { toast } from "sonner"
import { formatCurrency } from "@/lib/utils"
import {
    BookOpen,
    Package,
    TrendingUp,
    TrendingDown,
    AlertTriangle,
    Plus,
    Search,
    Calendar,
    Filter,
    ArrowUpCircle,
    ArrowDownCircle,
    RefreshCw,
} from "lucide-react"

// ──────────────────────────────────────────────────────────────────────────────
// TIPOS
// ──────────────────────────────────────────────────────────────────────────────
interface ProductoResumen {
    id: number
    nombre: string
    sku: string
    codigo_barras: string
    color: string
    stock_actual: number
    precio_compra: number
    precio_venta: number
    categoria_nombre: string
    tipo_prenda_nombre: string
    talla_valor: string
    proveedor_nombre: string
}

interface Movimiento {
    id: number
    fecha_movimiento: string
    tipo_movimiento: string
    tipo_legible: string
    cantidad: number
    motivo: string
    valor_unitario: number
    entrada_cantidad: number
    entrada_valor: number
    salida_cantidad: number
    salida_valor: number
    saldo_cantidad: number
    saldo_valor: number
    numero_venta?: string
    usuario_nombre?: string
}

interface ProductoDetalle extends ProductoResumen {
    descripcion?: string
    stock_minimo?: number
    stock_maximo?: number
    margen_ganancia?: number
}

// ──────────────────────────────────────────────────────────────────────────────
// HELPERS
// ──────────────────────────────────────────────────────────────────────────────
function formatFecha(fechaStr: string) {
    if (!fechaStr) return ""
    const d = new Date(fechaStr)
    return d.toLocaleDateString("es-EC", { day: "2-digit", month: "2-digit", year: "numeric" })
}

function getTipoBadgeVariant(tipo: string): "default" | "secondary" | "destructive" | "outline" {
    if (tipo.toLowerCase().includes("venta") || tipo.toLowerCase().includes("salida")) return "destructive"
    if (tipo.toLowerCase().includes("compra") || tipo.toLowerCase().includes("entrada")) return "default"
    if (tipo.toLowerCase().includes("devolucion") || tipo.toLowerCase().includes("devolución")) return "secondary"
    return "outline"
}

// ──────────────────────────────────────────────────────────────────────────────
// COMPONENTE PRINCIPAL
// ──────────────────────────────────────────────────────────────────────────────
export function KardexContent() {
    const [productos, setProductos] = useState<ProductoResumen[]>([])
    const [productoId, setProductoId] = useState("")
    const [productoDetalle, setProductoDetalle] = useState<ProductoDetalle | null>(null)
    const [movimientos, setMovimientos] = useState<Movimiento[]>([])
    const [resumen, setResumen] = useState<any>(null)
    const [loading, setLoading] = useState(false)

    // Filtros
    const [fechaInicio, setFechaInicio] = useState("")
    const [fechaFin, setFechaFin] = useState("")
    const [filtroTipo, setFiltroTipo] = useState("todos")

    // Modal ajuste
    const [ajusteOpen, setAjusteOpen] = useState(false)
    const [ajusteData, setAjusteData] = useState({
        tipo_movimiento: "ajuste_entrada",
        cantidad: "",
        motivo: "",
        valor_unitario: "",
    })
    const [ajusteLoading, setAjusteLoading] = useState(false)

    // Cargar lista de productos
    useEffect(() => {
        fetch("/api/kardex")
            .then((r) => r.json())
            .then((json) => {
                if (json.success) setProductos(json.data)
            })
            .catch(() => toast.error("Error al cargar productos"))
    }, [])

    // Cargar kardex del producto seleccionado
    const loadKardex = useCallback(async () => {
        if (!productoId) return
        setLoading(true)
        try {
            let url = `/api/kardex?producto_id=${productoId}`
            if (fechaInicio) url += `&fecha_inicio=${fechaInicio}`
            if (fechaFin) url += `&fecha_fin=${fechaFin}`
            const res = await fetch(url)
            const json = await res.json()
            if (json.success) {
                setProductoDetalle(json.producto)
                setMovimientos(json.movimientos)
                setResumen(json.resumen)
            } else {
                toast.error(json.error || "Error al cargar kardex")
            }
        } catch {
            toast.error("Error de conexión")
        } finally {
            setLoading(false)
        }
    }, [productoId, fechaInicio, fechaFin])

    useEffect(() => {
        loadKardex()
    }, [loadKardex])

    // Movimientos filtrados por tipo
    const movimientosFiltrados = filtroTipo === "todos"
        ? movimientos
        : movimientos.filter((m) => {
            if (filtroTipo === "entradas") return m.entrada_cantidad > 0
            if (filtroTipo === "salidas") return m.salida_cantidad > 0
            return m.tipo_legible.toLowerCase().includes(filtroTipo.toLowerCase())
        })

    // Registrar ajuste
    const handleAjuste = async () => {
        if (!ajusteData.cantidad || !ajusteData.motivo) {
            toast.error("Completa cantidad y motivo")
            return
        }
        setAjusteLoading(true)
        try {
            const res = await fetch("/api/kardex", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    producto_id: parseInt(productoId),
                    ...ajusteData,
                    cantidad: parseInt(ajusteData.cantidad),
                }),
            })
            const json = await res.json()
            if (!res.ok) { toast.error(json.error || "Error al registrar ajuste"); return }
            toast.success("Ajuste registrado correctamente")
            setAjusteOpen(false)
            setAjusteData({ tipo_movimiento: "ajuste_entrada", cantidad: "", motivo: "", valor_unitario: "" })
            loadKardex()
        } catch {
            toast.error("Error al registrar ajuste")
        } finally {
            setAjusteLoading(false)
        }
    }

    const stockBajo = productoDetalle && productoDetalle.stock_minimo != null
        ? productoDetalle.stock_actual <= (productoDetalle.stock_minimo || 0)
        : false

    return (
        <div className="flex flex-col gap-4 p-4 md:gap-6 md:p-6">
            {/* Header */}
            <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
                <div className="flex items-center gap-3">
                    <SidebarToggle />
                    <div>
                        <h1 className="text-2xl font-bold tracking-tight md:text-3xl flex items-center gap-2">
                            <BookOpen className="h-7 w-7 text-[#D4AF37]" />
                            Kardex de Inventario
                        </h1>
                        <p className="text-sm text-muted-foreground">Control detallado de movimientos por producto</p>
                    </div>
                </div>
                {productoId && (
                    <Button onClick={() => setAjusteOpen(true)} className="w-full md:w-auto">
                        <Plus className="mr-2 h-4 w-4" />
                        Registrar Ajuste
                    </Button>
                )}
            </div>

            {/* Selector de producto */}
            <Card>
                <CardHeader className="pb-3">
                    <CardTitle className="text-base flex items-center gap-2">
                        <Search className="h-4 w-4" />
                        Seleccionar Producto
                    </CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="grid grid-cols-1 md:grid-cols-4 gap-3">
                        <div className="md:col-span-2">
                            <SearchableSelect
                                value={productoId}
                                onValueChange={setProductoId}
                                items={productos.map((p) => ({
                                    value: p.id.toString(),
                                    label: `${p.nombre} | ${p.sku || "Sin SKU"} | Talla: ${p.talla_valor || "N/A"} | Color: ${p.color || "N/A"}`,
                                }))}
                                placeholder="Buscar producto..."
                                searchPlaceholder="Escriba nombre, SKU o código..."
                                emptyMessage="No se encontraron productos"
                            />
                        </div>
                        <div>
                            <div className="flex items-center gap-2">
                                <Calendar className="h-4 w-4 text-muted-foreground shrink-0" />
                                <Input
                                    type="date"
                                    value={fechaInicio}
                                    onChange={(e) => setFechaInicio(e.target.value)}
                                    placeholder="Desde"
                                />
                            </div>
                        </div>
                        <div>
                            <div className="flex items-center gap-2">
                                <Calendar className="h-4 w-4 text-muted-foreground shrink-0" />
                                <Input
                                    type="date"
                                    value={fechaFin}
                                    onChange={(e) => setFechaFin(e.target.value)}
                                    placeholder="Hasta"
                                />
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>

            {/* Encabezado del producto */}
            {productoDetalle && (
                <>
                    {/* Alerta stock bajo */}
                    {stockBajo && (
                        <div className="flex items-center gap-3 rounded-lg border border-orange-300 bg-orange-50 dark:bg-orange-950 p-3">
                            <AlertTriangle className="h-5 w-5 text-orange-500 shrink-0" />
                            <p className="text-sm font-medium text-orange-800 dark:text-orange-200">
                                ⚠️ Stock bajo: quedan <strong>{productoDetalle.stock_actual}</strong> unidades (mínimo: {productoDetalle.stock_minimo})
                            </p>
                        </div>
                    )}

                    {/* Ficha del producto */}
                    <Card>
                        <CardHeader className="pb-3">
                            <CardTitle className="text-base text-[#D4AF37]">📋 Ficha del Producto</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="grid grid-cols-2 md:grid-cols-4 gap-x-6 gap-y-3 text-sm">
                                <div>
                                    <span className="text-muted-foreground block text-xs">Nombre</span>
                                    <span className="font-semibold">{productoDetalle.nombre}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">SKU / Referencia</span>
                                    <span className="font-mono">{productoDetalle.sku || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Código de Barras</span>
                                    <span className="font-mono">{productoDetalle.codigo_barras || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Categoría</span>
                                    <span>{productoDetalle.categoria_nombre || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Tipo de Prenda</span>
                                    <span>{productoDetalle.tipo_prenda_nombre || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Talla</span>
                                    <span>{productoDetalle.talla_valor || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Color</span>
                                    <span>{productoDetalle.color || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Proveedor</span>
                                    <span>{productoDetalle.proveedor_nombre || "—"}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Unidad de Medida</span>
                                    <span>Pieza</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Precio de Compra</span>
                                    <span className="font-semibold">${formatCurrency(productoDetalle.precio_compra)}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Precio de Venta</span>
                                    <span className="font-semibold text-green-600">${formatCurrency(productoDetalle.precio_venta)}</span>
                                </div>
                                <div>
                                    <span className="text-muted-foreground block text-xs">Margen de Ganancia</span>
                                    <span className="font-semibold text-blue-600">
                                        {productoDetalle.margen_ganancia != null ? `${productoDetalle.margen_ganancia}%` : "—"}
                                    </span>
                                </div>
                            </div>
                        </CardContent>
                    </Card>

                    {/* Tarjetas resumen */}
                    {resumen && (
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                            <Card>
                                <CardContent className="pt-4">
                                    <div className="flex items-center gap-2">
                                        <ArrowUpCircle className="h-5 w-5 text-green-500" />
                                        <div>
                                            <p className="text-xs text-muted-foreground">Total Entradas</p>
                                            <p className="text-xl font-bold text-green-600">{resumen.total_entradas}</p>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                            <Card>
                                <CardContent className="pt-4">
                                    <div className="flex items-center gap-2">
                                        <ArrowDownCircle className="h-5 w-5 text-red-500" />
                                        <div>
                                            <p className="text-xs text-muted-foreground">Total Salidas</p>
                                            <p className="text-xl font-bold text-red-600">{resumen.total_salidas}</p>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                            <Card>
                                <CardContent className="pt-4">
                                    <div className="flex items-center gap-2">
                                        <Package className="h-5 w-5 text-blue-500" />
                                        <div>
                                            <p className="text-xs text-muted-foreground">Stock Actual</p>
                                            <p className={`text-xl font-bold ${stockBajo ? "text-orange-500" : "text-blue-600"}`}>
                                                {resumen.stock_actual}
                                            </p>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                            <Card>
                                <CardContent className="pt-4">
                                    <div className="flex items-center gap-2">
                                        <TrendingUp className="h-5 w-5 text-[#D4AF37]" />
                                        <div>
                                            <p className="text-xs text-muted-foreground">Valor en Inventario</p>
                                            <p className="text-xl font-bold text-[#D4AF37]">
                                                ${formatCurrency(resumen.stock_actual * (productoDetalle.precio_compra || 0))}
                                            </p>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                        </div>
                    )}

                    {/* Filtro por tipo de movimiento */}
                    <div className="flex items-center gap-3 flex-wrap">
                        <Filter className="h-4 w-4 text-muted-foreground" />
                        <span className="text-sm text-muted-foreground">Filtrar:</span>
                        {["todos", "entradas", "salidas", "Venta", "Compra", "Ajuste Entrada", "Ajuste Salida", "Devolución"].map((f) => (
                            <Button
                                key={f}
                                size="sm"
                                variant={filtroTipo === f ? "default" : "outline"}
                                onClick={() => setFiltroTipo(f)}
                                className="text-xs h-7"
                            >
                                {f === "todos" ? "Todos" : f}
                            </Button>
                        ))}
                        <Button
                            size="sm"
                            variant="ghost"
                            onClick={loadKardex}
                            disabled={loading}
                            className="ml-auto"
                        >
                            <RefreshCw className={`h-4 w-4 mr-1 ${loading ? "animate-spin" : ""}`} />
                            Actualizar
                        </Button>
                    </div>

                    {/* Tabla de movimientos */}
                    <Card>
                        <CardHeader className="pb-2">
                            <CardTitle className="text-base">
                                Tabla de Movimientos — Kardex
                            </CardTitle>
                        </CardHeader>
                        <CardContent className="overflow-x-auto">
                            {movimientosFiltrados.length === 0 ? (
                                <div className="py-10 text-center text-muted-foreground">
                                    <BookOpen className="h-10 w-10 mx-auto mb-2 opacity-30" />
                                    <p>No hay movimientos registrados para este producto{filtroTipo !== "todos" ? ` con ese filtro` : ""}.</p>
                                </div>
                            ) : (
                                <Table>
                                    <TableHeader>
                                        <TableRow>
                                            <TableHead className="text-center">Fecha</TableHead>
                                            <TableHead className="text-center">Tipo</TableHead>
                                            <TableHead>Detalle / Motivo</TableHead>
                                            <TableHead className="text-right">Valor Unit.</TableHead>
                                            <TableHead className="text-center bg-green-50 dark:bg-green-950/30" colSpan={2}>
                                                ↑ Entradas
                                            </TableHead>
                                            <TableHead className="text-center bg-red-50 dark:bg-red-950/30" colSpan={2}>
                                                ↓ Salidas
                                            </TableHead>
                                            <TableHead className="text-center bg-blue-50 dark:bg-blue-950/30" colSpan={2}>
                                                = Saldo
                                            </TableHead>
                                        </TableRow>
                                        <TableRow className="text-xs text-muted-foreground">
                                            <TableHead colSpan={4} />
                                            <TableHead className="text-right bg-green-50 dark:bg-green-950/30">Cant.</TableHead>
                                            <TableHead className="text-right bg-green-50 dark:bg-green-950/30">Valor</TableHead>
                                            <TableHead className="text-right bg-red-50 dark:bg-red-950/30">Cant.</TableHead>
                                            <TableHead className="text-right bg-red-50 dark:bg-red-950/30">Valor</TableHead>
                                            <TableHead className="text-right bg-blue-50 dark:bg-blue-950/30">Cant.</TableHead>
                                            <TableHead className="text-right bg-blue-50 dark:bg-blue-950/30">Valor</TableHead>
                                        </TableRow>
                                    </TableHeader>
                                    <TableBody>
                                        {movimientosFiltrados.map((mov) => (
                                            <TableRow key={mov.id}>
                                                <TableCell className="text-center text-sm whitespace-nowrap">
                                                    {formatFecha(mov.fecha_movimiento)}
                                                </TableCell>
                                                <TableCell className="text-center">
                                                    <Badge variant={getTipoBadgeVariant(mov.tipo_legible)} className="text-xs">
                                                        {mov.tipo_legible}
                                                    </Badge>
                                                </TableCell>
                                                <TableCell className="text-sm max-w-[200px]">
                                                    <span className="block truncate" title={mov.motivo}>
                                                        {mov.numero_venta ? `${mov.numero_venta} — ` : ""}
                                                        {mov.motivo || "—"}
                                                    </span>
                                                    {mov.usuario_nombre && (
                                                        <span className="text-xs text-muted-foreground">{mov.usuario_nombre}</span>
                                                    )}
                                                </TableCell>
                                                <TableCell className="text-right font-mono text-sm">
                                                    ${formatCurrency(mov.valor_unitario)}
                                                </TableCell>
                                                {/* Entradas */}
                                                <TableCell className="text-right font-semibold text-green-600 bg-green-50/50 dark:bg-green-950/20">
                                                    {mov.entrada_cantidad > 0 ? mov.entrada_cantidad : "—"}
                                                </TableCell>
                                                <TableCell className="text-right text-green-700 bg-green-50/50 dark:bg-green-950/20 font-mono text-sm">
                                                    {mov.entrada_cantidad > 0 ? `$${formatCurrency(mov.entrada_valor)}` : "—"}
                                                </TableCell>
                                                {/* Salidas */}
                                                <TableCell className="text-right font-semibold text-red-600 bg-red-50/50 dark:bg-red-950/20">
                                                    {mov.salida_cantidad > 0 ? mov.salida_cantidad : "—"}
                                                </TableCell>
                                                <TableCell className="text-right text-red-700 bg-red-50/50 dark:bg-red-950/20 font-mono text-sm">
                                                    {mov.salida_cantidad > 0 ? `$${formatCurrency(mov.salida_valor)}` : "—"}
                                                </TableCell>
                                                {/* Saldo */}
                                                <TableCell className="text-right font-bold text-blue-700 bg-blue-50/50 dark:bg-blue-950/20">
                                                    {mov.saldo_cantidad}
                                                </TableCell>
                                                <TableCell className="text-right text-blue-700 bg-blue-50/50 dark:bg-blue-950/20 font-mono text-sm font-semibold">
                                                    ${formatCurrency(mov.saldo_valor)}
                                                </TableCell>
                                            </TableRow>
                                        ))}
                                    </TableBody>
                                </Table>
                            )}
                        </CardContent>
                    </Card>
                </>
            )}

            {/* Estado vacío cuando no hay producto seleccionado */}
            {!productoId && (
                <Card>
                    <CardContent className="py-16 text-center">
                        <BookOpen className="h-14 w-14 mx-auto mb-4 text-[#D4AF37] opacity-40" />
                        <h3 className="text-lg font-semibold mb-1">Selecciona un producto</h3>
                        <p className="text-muted-foreground text-sm">
                            Elige un producto del selector para ver su kardex completo con entradas, salidas y saldo histórico.
                        </p>
                    </CardContent>
                </Card>
            )}

            {/* Modal ajuste de inventario */}
            <Dialog open={ajusteOpen} onOpenChange={setAjusteOpen}>
                <DialogContent className="max-w-md">
                    <DialogHeader>
                        <DialogTitle>Registrar Ajuste de Inventario</DialogTitle>
                        <DialogDescription>
                            Registra una entrada o salida manual de stock para <strong>{productoDetalle?.nombre}</strong>.
                        </DialogDescription>
                    </DialogHeader>
                    <div className="space-y-4 py-2">
                        <div className="space-y-1">
                            <Label>Tipo de Movimiento</Label>
                            <Select
                                value={ajusteData.tipo_movimiento}
                                onValueChange={(v) => setAjusteData((d) => ({ ...d, tipo_movimiento: v }))}
                            >
                                <SelectTrigger>
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="ajuste_entrada">Ajuste Entrada (sumar stock)</SelectItem>
                                    <SelectItem value="ajuste_salida">Ajuste Salida (restar stock)</SelectItem>
                                    <SelectItem value="devolucion_entrada">Devolución — Entrada</SelectItem>
                                    <SelectItem value="devolucion_salida">Devolución — Salida</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-1">
                            <Label>Cantidad *</Label>
                            <Input
                                type="number"
                                min="1"
                                value={ajusteData.cantidad}
                                onChange={(e) => setAjusteData((d) => ({ ...d, cantidad: e.target.value }))}
                                placeholder="Ej: 5"
                            />
                        </div>
                        <div className="space-y-1">
                            <Label>Motivo / Detalle *</Label>
                            <Input
                                value={ajusteData.motivo}
                                onChange={(e) => setAjusteData((d) => ({ ...d, motivo: e.target.value }))}
                                placeholder="Ej: Corrección de inventario, mercancía dañada..."
                                onKeyDown={(e) => { if (e.key === "Enter") { e.preventDefault(); handleAjuste() } }}
                            />
                        </div>
                    </div>
                    <DialogFooter>
                        <Button variant="outline" onClick={() => setAjusteOpen(false)}>Cancelar</Button>
                        <Button
                            onClick={handleAjuste}
                            disabled={ajusteLoading || !ajusteData.cantidad || !ajusteData.motivo}
                        >
                            {ajusteLoading ? "Registrando..." : "Registrar Ajuste"}
                        </Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>
        </div>
    )
}
